import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';

export const apiSlice = createApi({
  reducerPath: 'api',
  baseQuery: fetchBaseQuery({ baseUrl: '/' }),  // public à¦«à§‹à¦²à§à¦¡à¦¾à¦° à¦¥à§‡à¦•à§‡ fetch à¦•à¦°à¦¬à§‡
  endpoints: (builder) => ({
    getProducts: builder.query({
      query: () => 'products.json',  // public/products.json à¦«à¦¾à¦‡à¦² à¦¥à§‡à¦•à§‡ data
    }),
  }),
});

export const { useGetProductsQuery } = apiSlice;
